import {
  debounce,
  isPoneAvailable
} from '@/utils'
export default {
  data () {
    return {
      errorUsername: false,
      errorPassword: false,
      errorUsernameMsg: '',
      errorPasswordMsg: ''
    }
  },
  watch: {
    username () {
      this.debounceUsername()
    },
    password () {
      this.debouncePassword()
    }
  },
  methods: {
    validate () {
      this.debounceUsername = debounce(this.validateUsername)
      this.debouncePassword = debounce(this.validetePassword)
    },
    validateUsername () {
      if (isPoneAvailable(this.username)) {
        this.errorUsername = false
        this.errorUsernameMsg = ''
      } else {
        this.errorUsername = true
      }
    },
    validetePassword () {
      this.errorPassword = this.password.length < 6
      if (!this.errorPassword) {
        this.errorPasswordMsg = ''
      }
    }
  }
}
