import validate from './validate'
import Login from '@/mixins/login'
export default {
  mixins: [validate, Login],
  methods: {
    login() {
      if (this.errorUsername || !this.username) {
        this.errorUsernameMsg = '请输入账号'
      } else
      if (this.errorPassword || !this.password) {
        this.errorPasswordMsg = '请输入密码'
      } else {
        this.loginLM(this.username, this.password)
      }
    }
  }
}
