<template>
  <div class="login">
    <van-cell-group>
      <van-field
        v-model="username"
        clearable
        label="账号"
        type="tel"
        placeholder="请输入账号(手机号)"
        :error="errorUsername"
        :error-message="errorUsernameMsg"
        maxLength="11"
      />
      <van-field
        v-model="password"
        type="password"
        label="密码"
        placeholder="请输入密码"
        :error="errorPassword"
        :error-message="errorPasswordMsg"
        clearable
        @keyup.enter.native="login"
      />
    </van-cell-group>
    <div class="login-bt">
      <van-button
        size="large"
        type="primary"
        @click="login"
      >登录</van-button>
    </div>
    <div class="login-register">
      <van-button
        size="normal"
        plain
        type="primary"
        @click="jump('Register')"
      > 立即注册 </van-button>
    </div>
  </div>
</template>

<script>
import mixins from "./mixins";
export default {
  mixins: [mixins],
  data() {
    return {
      username: "",
      password: "",
      redirect: undefined
    };
  },
  watch: {
    $route: {
      handler: function(route) {
        this.redirect = route.query && route.query.redirect;
      },
      immediate: true
    }
  },
  mounted() {
    this.validate();
  }
};
</script>

